n-container[data-v-35ff6eb4] {\n    float: right;\n    padding: 10px 20px;\n    border-radius: 4px;\n    font-size: 15px;\n    font-weight: 500;\n    color: #757575;\n    background-color: white;\n    border: none;\n    margin: 16px 10px;\n}\n.create-button-contaner[data-v-35ff6eb4] {\n    float: right;\n    padding: 10px 20px;\n    border-radius: 4px;\n    font-size: 15px;\n    font-weight: 500;\n    color: white;\n    background: #151233;\n    border: none;\n    margin: 16px 0px;\n}\n.page-button-container[data-v-35ff6eb4] {\n    /* padding: 72px 30px; */\n    width: 177px;\n    height: 193px;\n    border-radius: 4px;\n    background-color: #eeeeee;\n    color: #d5d5d5;\n    border: 3px solid #eeeeee;\n}\n.page-button-container.active[data-v-35ff6eb4] {\n    border: 3px solid #D0D1E9;\n}\n.template-small-header[data-v-35ff6eb4] {\n    position: absolute;\n    top: 199px;\n    font: normal normal normal 16px/21px Roboto;\n    color: #616161;\n    width: 179px;\n    text-align: center;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/create/createNewScreen.vue"],"names":[],"mappings":";AAmMA;IACI,qBAAqB;AACzB;AAEA;IACI,kBAAkB;IAClB,2BAA2B;IAC3B,iCAAiC;AACrC;AAEA;IACI,4BAA4B;AAChC;AAEA;IACI,iBAAiB;IACjB,kBAAkB;IAClB,qBAAqB;IACrB,aAAa;AACjB;AAEA;IACI,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,mBAAmB;IACnB,kBAAkB;IAClB,WAAW;AACf;AAEA;IACI,YAAY;IACZ,aAAa;IACb,kBAAkB;AACtB;AAEA;IACI,UAAU;AACd;AAEA;IACI,+CAA+C;IAC/C,mBAAmB;AACvB;AAEA;IACI,gBAAgB;AACpB;AAEA;IACI,cAAc;IACd,eAAe;IACf,gBAAgB;AACpB;AAEA;IACI,WAAW;IACX,yBAAyB;IACzB,kBAAkB;IAClB,uBAAuB;IACvB,iBAAiB;AACrB;AAEA;IACI,8BAA8B;IAC9B,aAAa;IACb,iBAAiB;AACrB;AAEA;IACI,mBAAmB;IACnB,cAAc;IACd,eAAe;IACf,uBAAuB;IACvB,YAAY;IACZ,uBAAuB;IACvB,YAAY;IACZ,YAAY;IACZ,gBAAgB;IAChB,kBAAkB;IAClB,kBAAkB;AACtB;AAEA;IACI,yBAAyB;AAC7B;AAEA;IACI,yBAAyB;AAC7B;AAEA;IACI,gBAAgB;AACpB;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,kBAAkB;IAClB,eAAe;IACf,gBAAgB;IAChB,cAAc;IACd,uBAAuB;IACvB,YAAY;IACZ,iBAAiB;AACrB;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,kBAAkB;IAClB,eAAe;IACf,gBAAgB;IAChB,YAAY;IACZ,mBAAmB;IACnB,YAAY;IACZ,gBAAgB;AACpB;AAEA;IACI,wBAAwB;IACxB,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,yBAAyB;IACzB,cAAc;IACd,yBAAyB;AAC7B;AAEA;IACI,yBAAyB;AAC7B;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,2CAA2C;IAC3C,cAAc;IACd,YAAY;IACZ,kBAAkB;AACtB","sourcesContent":["<template>\n    <div class=\"dialog-container\">\n        <div class=\"input-label\">UI Page Name</div>\n        <IDEInput :isRequired=\"true\" :disable-required-icon=\"true\" @getValue=\"setNameValue\"\n            @validateStatus=\"setNameValidationStatus\" placeholder=\"\" :type=\"'text'\" info-text=\"\"\n            :validations=\"validations.name\" />\n        <hr style=\"margin: 32px 0px 32px 0px;\" />\n        <div style=\"margin-top: 32px\">\n            <div style=\"display: flex\">\n                <div>\n                    <button v-if=\"items.length > 0\"\n                        :class=\"{ 'button-text-container': true, active: selectedTabName == 'BLANK_PAGE' }\"\n                        @click=\"changeMainTab('BLANK_PAGE')\">\n                        Blank Page\n                    </button>\n                    <div v-for=\"subCategory in webItems\">\n                        <button :class=\"{ 'button-text-container': true, active: subCategory.name == selectedTabName, }\"\n                            @click=\"changeMainTab(subCategory.name)\">\n                            {{ subCategory.name }}\n                        </button>\n                    </div>\n                </div>\n                <div v-if=\"items.length > 0\" class=\"border-center\"></div>\n                <div class=\"template-container\">\n                    <div class=\"template-card-container\" v-if=\"selectedTabName == 'BLANK_PAGE' && items.length > 0\">\n                        <div :class=\"{ 'page-button-container': true, active: activeTemplateID == 'BLANK_PAGE' }\"\n                            @click=\"changeActiveTemplateID('BLANK_PAGE')\">\n                            <img src=\"./../../../../../images/blank_page.svg\" class=\"blank-page\" alt=\"Blank page\"/>\n                        </div>\n                        <p class=\"template-small-header\"> Blank Page </p>\n                    </div>\n                    <div class=\"template-card-container\" v-for=\"(child, index) in flattener(selectedTabName)\"\n                        :style=\"(index + 1) % 3 == 0 ? 'margin-right: 0px' : ''\">\n                        <div :class=\"{ 'page-button-container': true, active: activeTemplateID == child.ID }\"\n                            @click=\"changeActiveTemplateID(child.ID)\">\n                            <img class=\"template-image\" v-if=\"child.image\" :src=\"child.image\" :alt=\"child.name\" />\n                        </div>\n                        <p class=\"template-small-header\"> {{ child.name }} </p>\n                    </div>\n                </div>\n            </div>\n        </div>\n\n    </div>\n    <CreateDialogFooter :cancel-button-cb=\"() => emits('close')\" :create-button-cb=\"create\"\n        :create-button-enabled=\"isNameValid && templateSelected\" />\n</template>\n<script setup lang=\"ts\">\nimport { IPageItem, IPageTemplate, ITemplateCategoryItem } from \"@stechquick/algae/lib/qCloudTemp/template\";\nimport { onBeforeMount, ref } from \"vue\";\nimport { container } from \"../../../../../domain/core/diContainer\";\nimport { IModel } from \"../../../../../domain/model/models\";\nimport { ICreateModelParam, ICreateModel } from \"../../../../../domain/useCase/ICreateModel\";\nimport { IStudio } from \"../../../../../domain/useCase/IStudio\";\nimport { ITemplateUseCase } from \"../../../../../domain/useCase/ITemplateUseCase\";\nimport { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\nimport IDEInput from \"../../application/IDEInput.vue\";\nimport { IIDEInputDefinitions } from \"../../../../../domain/presentation/IIDEInputDefinitions\";\nimport CreateDialogFooter from \"./createDialogFooter.vue/createDialogFooter.vue\";\n\ninterface ICreateNewScreenInline {\n    parentId: string;\n}\n\nconst props = defineProps<ICreateNewScreenInline>();\nconst emits = defineEmits([\"close\"]);\n\nconst studio = container.resolve<IStudio>(IStudio);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst templateUseCase = container.resolve<ITemplateUseCase>(ITemplateUseCase);\n\nconst templates = ref<Array<IPageTemplate>>([]);\nconst items = ref<Array<ITemplateCategoryItem>>([]);\nconst webItems = ref<Array<ITemplateCategoryItem | IPageItem>>([]);\n\nvar selectedTabName = ref(\"BLANK_PAGE\");\nconst name = ref(\"\");\nconst isNameValid = ref<boolean>(false);\nconst activeTemplateID = ref(\"BLANK_PAGE\");\nconst templateSelected = ref<boolean>(false);\n\nconst validations = ref<Record<\"name\", IIDEInputDefinitions[\"validations\"]>>({\n    name: { minAndMaxLength: { min: 3, max: 40 }, availableCharacters: true, nonSpaceCharacter: true }\n});\n\nonBeforeMount(async () => {\n    executor.execute(\n        async () => {\n            templates.value = await templateUseCase.listTemplateInfos(\"page\") as Array<IPageTemplate>;\n            items.value = convert(templates.value);\n            webItems.value = items.value.find((i) => i.name == \"Web\")!.children;\n        },\n        { loading: true }\n    );\n});\n\nconst convert = (items: Array<IPageTemplate>) => {\n    const seperateToGroups = (targetItems: Array<IPageTemplate>, field: \"category\" | \"subCategory\"): Record<string, IPageTemplate[]> => {\n        const groups: Record<string, Array<IPageTemplate>> = targetItems.reduce((group, item) => {\n            const category = item[field];\n            const grp = group as any;\n            grp[category] = grp[category] ?? [];\n            grp[category].push(item);\n            return grp;\n        }, {});\n        return groups;\n    };\n    const retVal: Array<ITemplateCategoryItem> = [];\n    const categoryGroups = seperateToGroups(items, \"category\");\n    Object.keys(categoryGroups).forEach((groupName) => {\n        const subGroupItems = templates.value.filter((i) => i.category == groupName);\n        const subGroups = seperateToGroups(subGroupItems, \"subCategory\");\n        const children: Array<ITemplateCategoryItem> = Object.keys(subGroups).map((i) => {\n            const subGroup = subGroups[i];\n            const subCategoryName = subGroup[0].subCategory;\n            const pageItems = subGroupItems.filter((i) => i.subCategory == subCategoryName);\n            const subCategory: ITemplateCategoryItem = {\n                name: subCategoryName,\n                type: \"category\",\n                children: pageItems.map((page) => {\n                    return <IPageItem>{ ID: page.ID, name: page.name, type: \"page\", image: page.image };\n                }),\n            };\n            return subCategory;\n        });\n        retVal.push({ name: groupName, type: \"category\", children });\n    });\n\n    return retVal;\n};\n\nfunction create() {\n    if (!templateSelected.value || !isNameValid.value) return;\n    executor.execute(\n        async () => {\n            if (activeTemplateID.value == \"BLANK_PAGE\") {\n                const createObject: ICreateModelParam = {\n                    name: name.value,\n                    parentObjectId: props.parentId,\n                    modelBody: [{ key: \"qjson\", model: \"\" }],\n                    modelType: \"qjson\",\n                    modelAdditionals: { qjsonType: \"qjson\" },\n                };\n                const createdModel = await executor.executeUseCase<ICreateModel>(ICreateModel, createObject);\n                await onAfterModelCreated(createdModel);\n            } else {\n                const createdModel = await templateUseCase.createModelFromTemplate({\n                    name: name.value,\n                    parentObjectId: props.parentId,\n                    modelInfoID: activeTemplateID.value,\n                    modelAdditionals: { qjsonType: \"qjson\" },\n                    modelType: \"qjson\"\n                });\n                await onAfterModelCreated(createdModel);\n            }\n            emits(\"close\");\n        },\n        { loading: true }\n    );\n}\n\nfunction changeActiveTemplateID(id: string) {\n    activeTemplateID.value = id;\n    templateSelected.value = true;\n}\n\nfunction changeMainTab(selectedTab: string) {\n    selectedTabName.value = selectedTab;\n}\n\nfunction flattener(categoryName: String): Array<IPageTemplate> {\n    var arrayFlattener: Array<any> = [];\n    webItems.value.forEach((x) => {\n        if (x.name == categoryName) {\n            arrayFlattener.push((<ITemplateCategoryItem>x).children);\n        }\n    });\n    return arrayFlattener.flatMap((num) => num);\n}\n\nasync function onAfterModelCreated(createdModel: IModel) {\n    await studio.checkout(createdModel);\n    studio.openItem(createdModel);\n}\n\nfunction setNameValue(value: string) {\n    name.value = value;\n}\n\nfunction setNameValidationStatus(isValid: boolean) {\n    isNameValid.value = isValid;\n}\n</script>\n\n<style scoped>\n* {\n    font-family: \"Roboto\";\n}\n\n.blank-page{\n    position: absolute;\n    left: calc((100% - 68px)/2);\n    top: calc((100% - 27px - 90px)/2);\n}\n\n.dialog-container {\n    padding: 28px 20px 16px 26px;\n}\n\n.template-container {\n    margin-left: 29px;\n    position: relative;\n    overflow: hidden auto;\n    height: 464px;\n}\n\n.template-card-container {\n    width: 179px;\n    height: 220px;\n    margin-right: 20px;\n    margin-bottom: 20px;\n    position: relative;\n    float: left;\n}\n\n.template-image {\n    width: 170px;\n    height: 187px;\n    border-radius: 3px;\n}\n\n.template-container::-webkit-scrollbar {\n    width: 4px;\n}\n\n.template-container::-webkit-scrollbar-thumb {\n    background: #bcbcbc 0% 0% no-repeat padding-box;\n    border-radius: 10px;\n}\n\n.template-container::-webkit-scrollbar-thumb:hover {\n    background: #555;\n}\n\n.page-header-text {\n    color: #151233;\n    font-size: 14px;\n    font-weight: 500;\n}\n\n.name-bar {\n    width: 100%;\n    border: 1px solid #a1a1a1;\n    border-radius: 4px;\n    padding: 10px 10px 11px;\n    margin-left: -6px;\n}\n\n.border-center {\n    border-left: 1px solid #d5d5d5;\n    height: 464px;\n    margin-left: 20px;\n}\n\n.button-text-container {\n    margin-bottom: 16px;\n    color: #212121;\n    font-size: 16px;\n    font-weight: 400 bolder;\n    border: none;\n    background-color: white;\n    width: 138px;\n    height: 40px;\n    text-align: left;\n    border-radius: 4px;\n    padding-left: 10px;\n}\n\n.button-text-container:hover {\n    background-color: #e3e3e3;\n}\n\n.button-text-container.active {\n    background-color: #e3e3e3;\n}\n\n.bottom-side-container {\n    margin-top: 37px;\n}\n\n.cancel-button-container {\n    float: right;\n    padding: 10px 20px;\n    border-radius: 4px;\n    font-size: 15px;\n    font-weight: 500;\n    color: #757575;\n    background-color: white;\n    border: none;\n    margin: 16px 10px;\n}\n\n.create-button-contaner {\n    float: right;\n    padding: 10px 20px;\n    border-radius: 4px;\n    font-size: 15px;\n    font-weight: 500;\n    color: white;\n    background: #151233;\n    border: none;\n    margin: 16px 0px;\n}\n\n.page-button-container {\n    /* padding: 72px 30px; */\n    width: 177px;\n    height: 193px;\n    border-radius: 4px;\n    background-color: #eeeeee;\n    color: #d5d5d5;\n    border: 3px solid #eeeeee;\n}\n\n.page-button-container.active {\n    border: 3px solid #D0D1E9;\n}\n\n.template-small-header {\n    position: absolute;\n    top: 199px;\n    font: normal normal normal 16px/21px Roboto;\n    color: #616161;\n    width: 179px;\n    text-align: center;\n}\n</style>\n"],"sourceRoot":""}]);
